import {mapState} from 'vuex'
export default {
  name: 'pay_fail',
  components: {},
  inject: ['reload'], // 引入方法
  data () {
    return {
      msg: '支付失败啦'
    }
  },
  computed: {
    ...mapState({
    })
  },
  watch: {
  },
  mounted () {
    if (this.$route.params.error) {
      this.msg = this.$route.params.error
    }
  },
  destroyed () {
  },
  methods: {
  }
}



